import { useEffect, useState } from "react";
import {
  Container,
  Grid,
  Card,
  CardContent,
  Typography,
  Divider,
  Box,
  Stack,
} from "@mui/material";
import { bankPublic } from "../../../api/login/Login";

function CompanyBankDetails() {
  const [data, setData] = useState([]);

  const detailsBank = async () => {
    try {
      const res = await bankPublic();
      setData(res?.data?.data || []);
    } catch (error) {
      console.error("Error fetching bank details", error);
    }
  };

  useEffect(() => {
    detailsBank();
  }, []);

  return (
    <Container
     maxWidth="lg"
      sx={{
        minHeight: "100vh",
        backgroundColor: "var(--bs-primary3)",
        py: 4,
      }}
    >
      <Typography
        variant="h4"
        gutterBottom
        sx={{
          fontWeight: 700,
          color: "var(--bs-primary2)",
          textAlign: "center",
          mb: 4,
        }}
      >
        Company Bank Details
      </Typography>

      <Grid container spacing={3}>
        {data.map((item, index) => (
          <Grid item xs={12} sm={6} md={4} key={index}>
            <Card
              sx={{
                height: "100%",
                backgroundColor: "#ffffff",
                borderRadius: 3,
                border: "1px solid #e5e7eb",
                transition: "all 0.25s ease",
                "&:hover": {
                  boxShadow: "0 10px 24px rgba(0,0,0,0.08)",
                  transform: "translateY(-4px)",
                },
              }}
            >
              <CardContent sx={{ p: 3 }}>
                <Box textAlign="center" mb={2}>
                  <Typography
                    variant="h6"
                    sx={{ fontWeight: 700, color: "var(--bs-primary2)" }}
                  >
                    Unibcomp Networks Private Limited
                  </Typography>
                  <Divider sx={{ mt: 1 }} />
                </Box>

               <Stack spacing={1.2}>
                <InfoRow label="Bank Name" value={item.bank_name} />
                  <InfoRow label="Account Number" value={item.bank_account_number} />
                  <InfoRow label="IFSC Code" value={item.ifsc_code} />
                  <InfoRow label="Bank Code" value={item.bank_code} />
                  <InfoRow label="Branch Name" value={item.branch_name} />
                </Stack>

                {/* Remarks */}
                {item.remark && (
                  <Box
                    sx={{
                      mt: 2,
                      p: 1.5,
                      borderRadius: 2,
                      backgroundColor: "#f9fafb",
                      border: "1px dashed #d1d5db",
                    }}
                  >
                    <Typography
                      fontSize="13px"
                      fontWeight={600}
                      color="var(--bs-primary2)"
                    >
                      Remarks
                    </Typography>
                    <Typography fontSize="13px" color="#374151">
                      {item.remark}
                    </Typography>
                  </Box>
                )}
              </CardContent>
            </Card>
          </Grid>
        ))}
      {data.length === 0 && (
          <Grid item xs={12}>
            <Box
              sx={{
                textAlign: "center",
                py: 6,
                color: "var(--bs-primary2)",
              }}
            >
              <Typography variant="h6">No bank details available</Typography>
            </Box>
          </Grid>
        )}
      </Grid>
    </Container>
  );
}

/* Reusable row (UI only) */
const InfoRow = ({ label, value }) => (
  <Box
    sx={{
      display: "flex",
      justifyContent: "space-between",
      gap: 2,
      fontSize: "14px",
    }}
  >
    <Typography fontWeight={600} color="#374151">
      {label}
    </Typography>
    <Typography
      sx={{
        color: "#111827",
        fontWeight: 500,
        wordBreak: "break-all",
        textAlign: "right",
      }}
    >
      {value || "â€”"}
    </Typography>
  </Box>
);


export default CompanyBankDetails;
